@charset "UTF-8";
:root {
  --color-magenta: #ff0080;
  --color-blue: #25aff4;
  --color-green: #82cf17;
  --color-orange: #ff9500;
  --font-body: Roboto, "Helvetica Neue", sans-serif;
  --font-heading: Didot, "Didot LT STD", "Bodoni 72", Bodoni, "Bodoni MT", "Libre Bodoni", "Hoefler Text", Cambria, Georgia, serif;
  --font-mono: Consolas, Monaco, "Andale Mono", "Ubuntu Mono", monospace;
  --content-width: 900px; }

body > header {
  display: block;
  text-align: center;
  clip-path: polygon(0% 0%, 100% 0%, 100% calc(100% - 5em), 0% 100%);
  background: linear-gradient(transparent, rgba(0, 0, 0, 0.5)), url(../img/header.jpg) bottom/cover; }
  body > header a {
    color: inherit; }
  body > header .masthead {
    display: flex;
    justify-content: space-between; }
    body > header .masthead .logo img {
      display: block; }
      body > header .masthead .logo img:not(:hover) {
        filter: grayscale(100%) brightness(400%); }
  body > header .promo {
    padding: 5em;
    padding: 3em calc(50% - 16em) 5em;
    font-size: 150%; }
    body > header .promo .tagline {
      font-size: 280%;
      line-height: 1;
      margin-bottom: 0;
      font-weight: 100; }
    body > header .promo p {
      hyphens: manual;
      font-weight: 500; }
    body > header .promo .button {
      display: block;
      width: fit-content;
      margin: auto;
      padding: .5em 1em .4em;
      border-radius: .3em;
      text-align: center;
      text-transform: uppercase;
      text-decoration: none;
      font-weight: bold; }
      body > header .promo .button:not(:hover) {
        background: white;
        color: black;
        mix-blend-mode: screen; }
      body > header .promo .button:hover {
        background: #82cf17;
        color: white; }

#pros {
  flex-flow: row wrap;
  align-items: flex-start;
  padding: 0 calc(50vw - 450px - 1em); }
  #pros article {
    width: 450px;
    padding: .5em; }
    #pros article h1 {
      margin: 0;
      color: #ff0080;
      font: bold 150%/1 Didot, "Didot LT STD", "Bodoni 72", Bodoni, "Bodoni MT", "Libre Bodoni", "Hoefler Text", Cambria, Georgia, serif; }
      #pros article h1::before {
        content: "✓";
        display: inline-block;
        vertical-align: text-bottom;
        margin-right: .3em;
        width: 1em;
        background: #ff0080;
        color: white;
        border-radius: 50%;
        text-align: center;
        font-size: 135%;
        line-height: 1; }
    #pros article a {
      font-weight: bold; }
